.header-container {
    padding-top: 10px;
}

.logo {
    display: table-cell;
    vertical-align: middle;
    height: 75px !important;
    width: 100px !important;
    background: url(/assets/gualotuna-logo.svg) no-repeat;
    background-size: contain;
}

.logo--mobile {
    margin-left: 10px;
    height: 37px !important;
    width: 50px !important;
}

.main-header {
    margin-bottom: 0 !important;
}

.main-header--mobile {
    min-height: 66px;
}

.main-header--title {
    font-size: 3em !important;
    line-height: 40px;
}
